.header {
  display: flex;
  padding: 0.5rem;
  border-bottom: 1px solid #e52a33; }

.header__logo {
  background-image: url(https://0bba85283010b328c484-f139c7401b3658260f434c192b129add.ssl.cf3.rackcdn.com/pizzaplanetchatham.co.uk/manage/manage_uploads/upload/php4mKKUl.png);
  background-repeat: no-repeat;
  background-size: contain;
  flex-grow: 1;
  height: 2.5rem;
  margin: 0;
  padding: 0; }

.link {
  text-decoration: none;
  color: #fff; }

.menu-card {
  border: 0.125rem solid #f4f4f4;
  display: flex;
  flex-direction: row;
  margin: 0.75rem auto;
  padding: 0.75rem;
  width: 17.1875rem;
  color: #000; }
  .menu-card:hover {
    background: #e52a33;
    cursor: pointer; }
  .menu-card:focus {
    box-shadow: inset 0 0 0 2px #e52a33;
    outline: none; }

.menu-card__size,
.menu-card__description {
  flex: 1 1;
  display: flex;
  flex-direction: column; }

.menu-card__size-label,
.menu-card__size-name {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.menu-card__size-label {
  font-size: 2rem;
  text-transform: uppercase; }

.menu-card__description-maxToppings,
.menu-card__description-basePrice {
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.menu-card__description-basePrice {
  font-size: 1.25rem; }

.menu-card__description-maxToppings {
  font-size: 0.875rem; }

.menu-card__size-label,
.menu-card__size-name,
.menu-card__description-maxToppings,
.menu-card__description-basePrice {
  flex: 1 1; }

.menu-card:hover .menu-card__size-label,
.menu-card:hover .menu-card__size-name,
.menu-card:hover .menu-card__description-maxToppings,
.menu-card:hover .menu-card__description-basePrice {
  color: #fff; }

.menu__selection {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1; }

.menu__title {
  font-size: 1.75rem;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  margin: 0.9375rem 0; }

.menu {
  overflow-y: scroll;
  width: 75%; }

.menu__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(275px, 1fr)); }

@supports not (display: grid) {
  .menu {
    display: flex;
    justify-content: center;
    padding: 0 1.5rem; }
  .menu__list {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 915px; } }

/*---------------- @media -------------------*/
@media screen and (max-width: 527px) {
  .menu__title {
    font-size: 1rem; } }

.order-card {
  border: 0.125rem solid #f4f4f4;
  width: 50%;
  margin: 1rem 0; }

.order-card__buttons {
  display: flex;
  justify-content: center; }

.order-card__details {
  width: 100%;
  max-width: 22.5rem;
  margin: auto; }

.order-card__details-label {
  display: flex;
  justify-content: space-between;
  font-weight: 500; }

/*---------------- @media -------------------*/
@media screen and (max-width: 720px) {
  .order-card {
    width: 60%; } }

@media screen and (max-width: 600px) {
  .order-card {
    width: 70%; } }

@media screen and (max-width: 515px) {
  .order-card {
    width: 90%; } }

.order__selection {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1; }

.cart-card {
  border: 0.125rem solid #f4f4f4;
  width: 50%;
  margin: 0 0 1rem 0;
  padding: 0 0 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center; }

.cart-card__totalPrice {
  font-weight: 500; }

.cart__selection {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1; }

.cart__title {
  font-size: 1.75rem;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  margin: 0.9375rem 0; }

.footer {
  height: 0.625rem;
  padding: 0.875rem;
  background-color: #a3a3a3; }

body {
  margin: 0; }

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

