// @styles
@import '../../globals/scss/colors';
@import '../../globals/scss/spacing';
@import '../../globals/scss/typography';

.order-card {
    border: $spacing-4xs solid $color__light-gray;
    width: 50%;
    margin: rem(16px) 0;
}

.order-card__buttons {
    display: flex;
    justify-content: center;
}

.order-card__details {
    width: 100%;
    max-width: rem(360px);
    margin: auto;
}

.order-card__details-label {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
}

/*---------------- @media -------------------*/

@media screen and (max-width: 720px) {
    .order-card {
        width: 60%;
    }
}

@media screen and (max-width: 600px) {
    .order-card {
        width: 70%;
    }
}

@media screen and (max-width: 515px) {
    .order-card {
        width: 90%;
    }
}